<template>
  <div>
    <fTitle>
      <template #title>Progress 进度条</template>
      <template #introduce>用于展示操作进度，告知用户当前状态和预期。</template>
    </fTitle>
    <sTitle>
      <template #title>汽车油表demo</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
      <el-progress
      :width="300"
      :stroke-width="30"
      type="dashboard"
      :show-text=false
      :percentage="percentage"
      :color="colors">
    </el-progress>
    <div class="display"><i v-if="flag" class="el-icon-loading"></i>
    <i v-else class="el-icon-set-up">{{ percentage+'%' }}</i>
   </div>
<div>
  <el-button-group >
    <el-button :disabled="flag" icon="el-icon-truck" @click="decrease">启动汽车</el-button>
    <el-button :disabled="flag" @click="increase">给车加油<i class="el-icon-s-shop el-icon--right"></i></el-button>
  </el-button-group>
</div>
    </el-col></el-row>
    </div></div>
  </div>
<!-- el-icon-truck el-icon-loading el-icon-s-shop -->
</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      percentage: 10,
      flag: false,
      colors: [
        { color: '#f56c6c', percentage: 20 },
        { color: '#e6a23c', percentage: 40 },
        { color: '#5cb87a', percentage: 60 },
        { color: '#1989fa', percentage: 80 },
        { color: '#6f7ad3', percentage: 100 }
      ]
    }
  },
  methods: {
    increase () {
      this.flag = true
      const time = setInterval(() => {
        this.percentage += 5
        if (this.percentage > 100) {
          this.percentage = 100
          clearInterval(time)
          this.flag = false
          alert('油已经加满啦')
        }
      }, 500)
    },
    decrease () {
      this.flag = true
      const time = setInterval(() => {
        this.percentage -= 5
        if (this.percentage < 0) {
          this.percentage = 0
          clearInterval(time)
          this.flag = false
          alert('车没油了哦')
        }
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .el-button{
        font-size: 30PX;
        align-items: center;
      }
      .display{
        position: relative;
        top: -45%;
        font-size: 30px;
      }
    }}
</style>
